<template>
  <v-app>
    <v-navigation-drawer
      v-model="drawer"
      :color="
        !$vuetify.theme.dark ? 'blue-grey darken-3' : 'grey darken-3'
      "
      :mini-variant="mini"
      dark
      app
    >
      <template v-slot:prepend>
        <v-list-item 
          dense 
          style="padding-top: 3px"
        >
          <v-list-item-action v-if="!mini">
            <v-icon 
              large 
              color="accent"
            >
              mdi-view-dashboard
            </v-icon>
          </v-list-item-action>
          <v-list-item-content v-if="!mini">
            <v-list-item-title>
              <h2>Dashboard</h2>
            </v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
            <v-btn 
              icon 
              @click.stop="mini = !mini"
            >
              <v-icon v-html="mini ? 'mdi-chevron-right' : 'mdi-chevron-left'"></v-icon>
            </v-btn>
          </v-list-item-action>
        </v-list-item>
      </template>
      <v-divider></v-divider>  
      
      <v-list dense>
        <v-tooltip 
          v-for="(item, index) of items"  
          :key="index" 
          :disabled="!mini"
          right
          >
          <template v-slot:activator="{ on }">
            <v-list-item 
              :to="item.route" 
              v-on="on"
            >
              <v-list-item-action>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
          <span>{{ item.title }}</span>
        </v-tooltip>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      flat
      app
      :style="
        !$vuetify.theme.dark ? 'border-bottom: 0.1px solid #9C9C9C' : 'border-bottom: 0.1px solid #595959'
      "
    >
      <v-app-bar-nav-icon 
        class="hidden-lg-and-up"
        @click.stop="drawer = !drawer"
      />
      <v-toolbar-title>Admin</v-toolbar-title>
      <v-spacer></v-spacer>
      
      <v-menu
        left
        bottom
      >
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list>
          <v-list-item @click="$vuetify.theme.dark = false">
            <v-list-item-title>Светлая тема</v-list-item-title>
          </v-list-item>
          <v-list-item @click="$vuetify.theme.dark = true">
            <v-list-item-title>Темная тема</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>

    <v-content>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    drawer: null,
    mini: false,
    items: [
      {
        route: '/users',
        icon: 'mdi-account',
        title: 'Пользователи'
      },
      {
        route: '/games-history',
        icon: 'mdi-gamepad-variant',
        title: 'История всех игр'
      },
      {
        route: '/crash-history',
        icon: 'mdi-chart-bell-curve-cumulative',
        title: 'История Crash'
      },
      {
        route: '/withdrawal',
        icon: 'mdi-cash-multiple',
        title: 'Заявки на вывод средств'
      },
      {
        route: '/logs',
        icon: 'mdi-login-variant',
        title: 'Логи'
      },
      {
        route: '/statistics',
        icon: 'mdi-information-outline',
        title: 'Общая статистика'
      }
    ]
  })
}
</script>
