import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/users'
  },
  {
    path: '/users',
    component: () => import('../views/Users.vue')
  },
  {
    path: '/users/:id',
    name: 'users',
    component: () => import('../views/Profile.vue'),
    props: true
  },
  {
    path: '/games-history',
    component: () => import('../views/Games.vue')
  },
  {
    path: '/crash-history',
    component: () => import('../views/CrashHistory.vue')
  },
  {
    path: '/crash-games/:id',
    name: 'crash-games',
    component: () => import('../views/CrashGame.vue'),
    props: true
  },
  {
    path: '/withdrawal',
    component: () => import('../views/Withdrawal.vue')
  },
  {
    path: '/logs',
    component: () => import('../views/Logs.vue')
  },
  {
    path: '/statistics',
    component: () => import('../views/Stats.vue')
  },
  {
    path: '*',
    redirect: '/404'
  },
  {
    path: '/404',
    component: () => import('../views/404.vue')
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    return { x: 0, y: 0 };
  }
});

export default router;
